.loader {
  box-sizing: border-box;
  background: linear-gradient(0deg, rgba(255, 61, 0, .2) 33%, #ff3d00 100%);
  border-radius: 50%;
  width: 48px;
  height: 48px;
  animation: 1s linear infinite rotation;
  display: block;
  position: absolute;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  background: #263238;
  border-radius: 50%;
  width: 44px;
  height: 44px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

